header {
    // позиция на сайте
    margin: 200px;  
    font-family: Cuprum,Arial, Helvetica, sans-serif;

    .dws-menu ul {
    // убрать точки
list-style: none;
    //    разместить по горизонтали
display: flex;
justify-content: center;

li {
position: relative;
// разделитель между меню
border-right: 1px solid #1257ea;
// при наведении мышкой
a:hover {
 background: rgb(202, 128, 128); 
 color:#fff;  
//  тень под кнопкой
 box-shadow: 1px 5px 10px -5px black;
//  плавное появление
transition: all 0.3s ease;
}
}
// первый и последний бардюр меню
li:first-child {
    border-left: 2px solid #1257ea;
    }
li:last-child {
    border-right: 2px solid #1257ea;  
    }


li a {
  display: block;
      //   цвет меню
  background: rgb(156, 147, 147);
//   отступы между кнопками
  padding: 15px 20px 15px 20px;
  font-size: 18px;
  color:aqua;
//   убрать подчеркивание
  text-decoration: none;
//   заглавные буквы
  text-transform: uppercase;
//   плавное исчезание тени
  transition: all 0.3s ease;

//   позиция иконок списка
a i.fa {
    position: absolute;
    top: 15px;
    left: 12px;        
}
// размер иконок
img {
    width: 20px;
    height: 20px;
    }

}
}

}
// подменю
.dws-menu li ul {
    display:block;
    position: absolute;
    // ширина подменю
    min-width:150px;
    display: none;
  
    li {
        border: 1px solid #dd8119;
        
a {
padding:10px ;
text-transform: none;
// цвет подменю
background: rgb(183, 172, 211);
    }

    }
    
}

// меню подменю
.dws-menu li ul li ul {
position: absolute;
right: -150px;
top: 0;

}
.dws-menu li:hover > ul {
            display: block;
        }